/* eslint-disable eqeqeq */
import { useContext, useState } from 'react';
import Navbar from '../Home/Navbar'
import { apiValue } from '../Data/AllData';
import { useParams } from 'react-router';
import { useNavigate } from "react-router-dom";
import { CartProvider, useCart } from 'react-use-cart';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSpinner } from '@fortawesome/free-solid-svg-icons';


function Page() {
  const { addItem } = useCart();
  const { Api, isLoading, loading } = useContext(apiValue)
  const { id } = useParams();
  const [imgLoaded, setimgLoaded] = useState(false)
  console.log(Api);

  const Navigate = useNavigate();
  if (isLoading && loading) {
    return (
      <div className='myBox'>
        <div className="large centered row d-flex justify-content-center align-items-center vh-100">
          <svg width="128" height="128" viewBox="0 0 128 128">
            <filter id="displacementFilter">
              <feTurbulence type="turbulence" numOctaves="2" baseFrequency="0" result="turbulence" />
              <feDisplacementMap in2="turbulence" in="SourceGraphic" scale="1" xChannelSelector="R" yChannelSelector="G" />
            </filter>
            <polygon points="64 128 8.574 96 8.574 32 64 0 119.426 32 119.426 96" fill="red" />
          </svg>
        </div>
      </div>
    );
  }

  return (
    <div>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <div className='container'>
        <button onClick={() => { Navigate(-1) }} className='btn btn-success rounded'>
          <i className="fa-solid fa-backward"></i> Back
        </button>
        {Api.map((x) => {
          return x.id == id ? (

            <div className='row text-center' key={x.id}>
              <div className='col-md-6 position-relative'>
                <div className="position-absolute rounded-start-2 p-2" style={{ right: "0%", borderRadius: "50px", backgroundColor: "skyblue" }}><i className="fa-solid fa-star fa-beat bg-transparent"></i>{x.rating.toFixed(1)}</div>
                {!imgLoaded && <div className='col-md-6' style={{
                  position: "absolute",
                  top: "50%",
                  left: "50%",
                  transform: "translate(-50%, -50%)"
                }}>Loading image...<FontAwesomeIcon icon={faSpinner} spin /></div>}
                <img className='w-100' src={x.images[0]} alt="" onLoad={() => setimgLoaded(true)}/>
              </div>
              <div className='col-md-5 my-5'>
                <h3 className='my-5'> <span className='text-info'>Title: </span>{x.title}</h3>
                <h3 className='mb-5'> <span className='text-info'>Brand: </span>{x.brand}</h3>
                <h3 className='mb-5'> <span className='text-info'>Price: </span>{x.price}$</h3>
                <h3 className='mb-5'> <span className='text-info'>Category: </span>{x.category}</h3>
                <h3 className='mb-5'> <span className='text-info'>ID: </span>:{x.id}</h3>
                <h3 className='my-2'><span className='text-info'>Width: </span> {x.weight}</h3>
                <button onClick={() => { addItem(x) }} className='btn btn-primary btn-block mt-5'>Add to Cart</button>
              </div>
              <div className='col-md-12 m-auto text-center'>
                <h1 className='text-black-50 my-5'>{x.title} </h1>
                {!imgLoaded && <div className='d-flex justify-content-center align-items-center'>Loading image...<FontAwesomeIcon icon={faSpinner} spin /></div>}
                <img className='w-75' src={x.images[0]} alt="" onLoad={() => setimgLoaded(true)}/>
                <h3 className='my-5 text-left text-info'>Description:-</h3>
                <h3 className='my-2'>{x.description}</h3>
                <br></br>

              </div>
            </div>
          ) : null
        })}
      </div>

    </div>
  )
}
function AllSingleProduct() {



  return (
    <CartProvider>
      <Navbar />
      <Page />
    </CartProvider>
  )
}

export default AllSingleProduct