import React from 'react'
import Navbar from '../Home/Navbar'
import Footer from '../Home/Footer'
import { CartProvider } from 'react-use-cart';

function error() {
    return (
        <CartProvider>
            <Navbar/>
            <div className="text-center vh-100 d-flex justify-content-center align-items-center flex-column">
                <i className="fa-solid fa-cog fa-spin" style={{
                    fontSize: "50px",
                    margin: "20px",
                    color: "blue",
                }}></i>
                <h1 className="text-fa-bold">
                    {" "} <span className="text-danger">Not Found</span>
                    <i className="fa-solid fa-face-frown-open" style={{
                        color: "red",
                    }}></i>{" "}
                </h1>
                <p>
                    This page doesn't exist.{" "}
                    <i className="fa-solid fa-circle-exclamation"></i>
                </p>
                <p>If this is a mistake, let us know, and we will try to fix it!</p>
            </div>
                <Footer/>
        </CartProvider>
    )
}

export default error