import { CartProvider, useCart } from "react-use-cart";
import Navbar from "../Home/Navbar";
import { Link } from "react-router";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";
import { useContext, useState } from "react";
import { apiValue } from "../Data/AllData";

function Cart() {
    // eslint-disable-next-line no-unused-vars
    const { Api, isLoading , loading} = useContext(apiValue);
    const {
        emptyCart,
        isEmpty,
        totalUniqueItems,
        items,
        totalItems,
        cartTotal,
        updateItemQuantity,
        removeItem,
    } = useCart();
    const [imgLoaded, setimgLoaded] = useState(false)
    if (isLoading && loading) {
        return (
            <div className='myBox'>
                <div className="large centered row d-flex justify-content-center align-items-center vh-100">
                    <svg width="128" height="128" viewBox="0 0 128 128">
                        <filter id="displacementFilter">
                            <feTurbulence type="turbulence" numOctaves="2" baseFrequency="0" result="turbulence" />
                            <feDisplacementMap in2="turbulence" in="SourceGraphic" scale="1" xChannelSelector="R" yChannelSelector="G" />
                        </filter>
                        <polygon points="64 128 8.574 96 8.574 32 64 0 119.426 32 119.426 96" fill="red" />
                    </svg>
                </div>
            </div>
        );
    }
    if (isEmpty) return <div className="text-center container">
        <div className="row">
            <div className="col-md-10">
                <h1 className="text-center ">Your cart is empty</h1>
                <img className="text-center" src="https://cdn-icons-png.flaticon.com/512/11329/11329060.png" alt=""></img>
                <Link to="/Product" className="btn btn-primary btn-block">Go to Shop</Link>
            </div>
        </div>
    </div>
    return (
        <div>
            <h1 className="text-center">Cart <i className="fa-solid fa-cart-shopping fa-bounce" style={{ color: "red" }}></i></h1>
            <div className='mt-5 container-fluid'>
                <h1 className="ml-5"><span className="mb-3">Total Items </span><span style={{ color: "red" }}>{totalUniqueItems}</span><button className="btn btn-danger" style={{ position: "absolute", right: "5%", padding: "10px" }} onClick={emptyCart}>Clear Cart</button></h1>

                <div className='row'>
                    <div className='col-md-11 m-auto text-center'>
                        <table className="table table-info table-bordered table-hover">
                            <thead>
                                <tr>
                                    <th scope="col">ID</th>
                                    <th scope="col">Name</th>
                                    <th scope="col">Image</th>
                                    <th scope="col">Price</th>
                                    <th scope="col">Total Price</th>
                                    <th scope="col">Quantity</th>
                                    <th scope="col">Operation</th>
                                </tr>
                            </thead>
                            {items.map((item) => (
                                <tbody key={item.id}>
                                    <tr >
                                        <th scope="row">{item.id}</th>
                                        <td><Link to={`/singleproduct/${item.id}`} style={{ color: "black" }}><h4>{item.title}</h4></Link></td>
                                        <td>
                                            {!imgLoaded && <div className='position-absolute'>Loading image...<FontAwesomeIcon icon={faSpinner} spin /></div>}
                                            <img src={item.images[0]} alt={item.title} onLoad={() => setimgLoaded(true)} style={{ width: "150px" }}></img>
                                        </td>
                                        <td><h5>{Math.ceil(item.price)}$</h5></td>
                                        <td><h5>{Math.ceil(item.price) * item.quantity}$</h5></td>
                                        <td><h5>{item.quantity}</h5></td>
                                        <td>
                                            <button className='btn btn-info'
                                                onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) + 1)}
                                            >
                                                +
                                            </button>
                                            <button className='mx-2 btn btn-warning'
                                                onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) - 1)}
                                            >
                                                -
                                            </button>
                                            <button className='btn btn-danger' onClick={() => removeItem(item.id)}>&times;</button></td>
                                    </tr>
                                </tbody>
                            ))}
                            <tfoot>
                                <tr>
                                    <td colSpan={4}><h4>----------  Total  ----------</h4></td>
                                    <td><h4>{Math.ceil(cartTotal)}$</h4></td>
                                    <td><h4>{totalItems}</h4></td>
                                    <td><Link to="/Product" className="btn btn-primary">Go to Shop</Link></td>
                                </tr>
                            </tfoot>
                        </table>
                    </div>
                </div>
            </div >
        </div>
    );
}

function AllCart() {
    return (
        <CartProvider>
            <Navbar />
            <br />
            <br />
            <br />
            <br />
            <Cart />
        </CartProvider>
    );
}
export default AllCart