import Navbar from './Home/Navbar'
import Header from './Home/Header'
import About from './Home/About'
import Device from './Home/Device'
import Age from './Home/Age'
import Download from './Home/Download'
import Apped from './Home/Apped'
import Footer from './Home/Footer'
import { CartProvider } from 'react-use-cart';
import { useContext } from 'react'
import { apiValue } from './Data/AllData'


function App() {
  // eslint-disable-next-line no-unused-vars
  const { Api, isLoading, loading } = useContext(apiValue);

  if (isLoading && loading) {
    return (
      <div className='myBox'>
        <div className="large centered row d-flex justify-content-center align-items-center vh-100">
          <svg width="128" height="128" viewBox="0 0 128 128">
            <filter id="displacementFilter">
              <feTurbulence type="turbulence" numOctaves="2" baseFrequency="0" result="turbulence" />
              <feDisplacementMap in2="turbulence" in="SourceGraphic" scale="1" xChannelSelector="R" yChannelSelector="G" />
            </filter>
            <polygon points="64 128 8.574 96 8.574 32 64 0 119.426 32 119.426 96" fill="red" />
          </svg>
        </div>
      </div>
    );
  }
  return (
    <CartProvider>
      <Navbar />
      <Header />
      <About />
      <Device />
      <Age />
      <Download />
      <Apped />
      <Footer />
    </CartProvider>
  )
}

export default App