/* eslint-disable eqeqeq */
import React, { useContext, useState } from 'react'
import Navbar from '../Home/Navbar'
import { Link, } from 'react-router';
import { apiValue } from '../Data/AllData';
import { CartProvider, useCart } from 'react-use-cart';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSpinner } from '@fortawesome/free-solid-svg-icons';
import '../product/AllProducat.css'

function Page() {
  const { addItem } = useCart();
  const { Api,isLoading , loading} = useContext(apiValue);
  const [search, setsearch] = useState("");
  const [imgLoaded, setimgLoaded] = useState(false);


  const getsearch = (e) => {
    setsearch(e.target.value)
  }


  if (isLoading && loading) {
    return (
      <div className='myBox'>
        <div className="large centered row d-flex justify-content-center align-items-center vh-100">
          <svg width="128" height="128" viewBox="0 0 128 128">
            <filter id="displacementFilter">
              <feTurbulence type="turbulence" numOctaves="2" baseFrequency="0" result="turbulence" />
              <feDisplacementMap in2="turbulence" in="SourceGraphic" scale="1" xChannelSelector="R" yChannelSelector="G" />
            </filter>
            <polygon points="64 128 8.574 96 8.574 32 64 0 119.426 32 119.426 96" fill="red" />
          </svg>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className='container'>
        <div className='row'>
          <div className='col-md-10 m-auto'>
            <input className='form-control' placeholder='Search' onChange={getsearch} />
          </div>
          {Api.map((x) => {
            return search == 0 ? (
              <div className='col-md-4 text-center mb-5 position-relative' key={x.id}>
                {!imgLoaded && <div className='loading'>Loading image...<FontAwesomeIcon icon={faSpinner} spin /></div>}
                <img className='w-100 h-75' src={x.images[0]} alt="" onLoad={() => setimgLoaded(true)} />
                <h5>{x.title}</h5>
                <h5>{x.brand}</h5>
                <h5>{x.price}$</h5>
                <h5>{x.id}</h5>
                <Link to={`/singleproduct/${x.id}`} className='btn btn-dark'>Details</Link>
                <button onClick={() => { addItem(x) }} className='btn btn-primary mx-2'>Add to Cart</button>
              </div>
            ) : x.id == search ? (
              <div className='col-md-4 text-center' key={x.id}>
                <img className='w-100 h-75' src={x.images[0]} alt="" />
                <h5>{x.title}</h5>
                <h5>{x.brand}</h5>
                <h5>{x.price}$</h5>
                <h5>{x.id}</h5>
                <Link to={`/singleproduct/${x.id}`} className='btn btn-dark'>Details</Link>
                <button onClick={() => { addItem(x) }} className='btn btn-primary mx-2'>Add to Cart</button>
              </div>
            ) : x.title.toLowerCase().includes(search.toLowerCase()) ? (
              <div className='col-md-4 text-center' key={x.id}>
                <img className='w-100 h-75' src={x.images[0]} alt="" />
                <h5>{x.title}</h5>
                <h5>{x.brand}</h5>
                <h5>{x.price}$</h5>
                <h5>{x.id}</h5>
                <Link to={`/singleproduct/${x.id}`} className='btn btn-dark'>Details</Link>
                <button onClick={() => { addItem(x) }} className='btn btn-primary mx-2'>Add to Cart</button>
              </div>
            ) : null
          })}
        </div>
      </div>
    </div>
  )
}

function AllProducts() {
  return (
    <CartProvider>
      <Navbar />
      <br />
      <br />
      <br />
      <br />
      <br />
      <Page />
    </CartProvider>
  );
}

export default AllProducts